import * as crc32 from 'crc-32';

export class DirSync {
  #handle;
  #files = new Map();

  constructor(handle) {
    this.#handle = handle;
  }

  async loadFiles() {
    await this.#listFiles();
    console.log(this.#files);
  }

  async sendAll(sender) {
    for (const file of Array.from(this.#files.values())) {
      await this.sendFile(file, sender);
    }
  }

  async #listFiles() {
    this.#files.clear();
    for await (const entry of this.#handle.values()) {
      if (entry.kind === 'file') {
        const handle = await entry.getFile();
        const id = crc32.str(handle.name);
        this.#files.set(id, {
          name: handle.name,
          size: handle.size,
          handle,
          local: true,
        });
      }
    }
  }

  async sendFile(file, sender) {
    let fileOffset = 0;
    let sizeRemaining = file.size;

    sender({
      type: 'file',
      value: {
        name: file.name,
        size: file.size
      }
    });

    const id = crc32.str(file.name);
    while (sizeRemaining > 0) {
      const headerBuffer = new ArrayBuffer(8);
      const headerView = new DataView(headerBuffer);
      headerView.setInt32(0, id, true);
      headerView.setUint32(4, fileOffset, true);
      let chunkSize = 8192;
      if (sizeRemaining < chunkSize) {
        chunkSize = sizeRemaining;
      }
      const chunk = file.handle.slice(fileOffset, chunkSize + fileOffset);
      fileOffset += chunkSize;
      sizeRemaining -= chunkSize;

      const blob = new Blob([headerBuffer, chunk]);

      sender(await blob.arrayBuffer());
    }
  }

  async createFile(metadata) {
    const id = crc32.str(metadata.name);
    if (this.#files.has(id) && this.#files.get(id).local) {
      return;
    }
    const handle = await this.#handle.getFileHandle(metadata.name, { create: true });
    const writeable = await handle.createWritable();
    this.#files.set(id, {
      name: metadata.name,
      size: metadata.size,
      written: 0,
      handle,
      writeable,
      local: false
    });
  }

  async writeFile(buffer) {
    const headerBuffer = buffer.slice(0, 8);
    const headerView = new DataView(headerBuffer);
    const id = headerView.getInt32(0, true);
    const offset = headerView.getUint32(4, true);
    const chunk = buffer.slice(8);
    const file = this.#files.get(id);
    if (!file || !file.writeable) {
      return;
    }
    await file.writeable.seek(offset);
    await file.writeable.write(chunk);
    file.written += chunk.byteLength;
    console.log(`written ${file.written} out of ${file.size}`);
    if (file.written === file.size) {
      await file.writeable.close();
      this.#files.set(id, {
        name: file.name,
        size: file.size,
        handle: file.handle,
        local: false
      });
    }
  }

  static async open() {
    const dirHandle = await showDirectoryPicker({ mode: 'readwrite' });
    const dirSync = new DirSync(dirHandle);
    await dirSync.loadFiles();
    return dirSync;
  }
}
